pe=radio]:checked + label .tab-text {\n      color: $tab-active-color;\n      background: $tab-active-bg;\n    }\n\n    > input[type=radio]:checked + label {\n      .step-counter {\n        color: $tab-base-bg;\n        border-color: $tab-base-bg;\n\n        &:before {\n          border-color: $tab-base-bg;\n        }\n      }\n    }\n\n    #tab1:checked ~ #content1,\n    #tab2:checked ~ #content2,\n    #tab3:checked ~ #content3,\n    #tab4:checked ~ #content4 {\n      display: block;\n    }\n\n}\n\n\n// RESPONSIVE UTILS\n@media screen and (min-width: 769px) {\n  .is-md-hidden{\n    display: none !important;\n  }\n}\n\n\n@media screen and (max-width: 63.938em) { // = 1024px\n\n    // TABS RWD\n    .code-tabs {\n        > label {\n            width: 25%;\n\n            .tab-text {\n              padding: 10px 23px;\n            }\n\n            .tab-text br {\n              display: inline;\n            }\n        }\n    }\n}\n\n@media screen and (max-width: 48em) {\n    // hiding tabs on small screen devices\n    // leaving only step counters which can be tapped\n    .code-tabs {\n        > label {\n          height: 1px;\n          float: left;\n\n          .tab-text {\n            display: none;\n          }\n        }\n    }\n}","// ANIMATON\n\n// addAnimation\n@mixin addAnimation($property, $duration: .3s, $ease-func: ease-out) {\n  transition: $property $duration $ease-func;\n}\n",".documentation-page {\n  font-size: .875rem;\n\n  .toc {\n    display: block !important; // overwrite docbook style\n\n    #toctitle {\n\t    display: none;\n    }\n\n    .sectlevel2 {\n        padding-left: 10px;\n        // font-size: $small-font-size;\n    }\n  }\n\n  #cd-docs-nav {\n  \t\tposition: sticky;\n  \t\toverflow-y: auto;\n  \t\toverflow-x: hidden;\n  \t\tleft: 0;\n\n  \t\t// 0 level links\n  \t\t.cd-toc-link {\n  \t\t\tdisplay: inline-block;\n  \t        padding: .5rem 2rem .5rem 0;\n      \t\tline-height: 1.1;\n\n  \t\t\t&.active {\n  \t\t\t\tpadding-bottom: 0;\n  \t\t